import {key1} from '../credentials.json';
const baseURLFinn = 'https://finnhub.io/api/v1';
const baseURLFire = 'https://us-central1-chang-liu-1.cloudfunctions.net'

export const getAnalysis = (symbol,onSuccess,onError) => {
  fetch(baseURLFinn+`/stock/upgrade-downgrade?symbol=${symbol}&token=${key1}`)
    .then(res => {
      res.json().then(data => onSuccess(data)).catch(err=>onError(err.message));
    })
    .catch(err => onError(err.message));
}

export const getStockRecommendations = (onSuccess,onError) => {
  fetch(baseURLFire+'/getStockRecommendations')
    .then(res=>{
      res.json()
        .then(data =>{
          onSuccess(data);
        }).catch(err=>{
          onError(err.message);
        });
    }).catch(err=>{
      onError(err.message);
    });
}