import React from 'react';
import {VictoryBar,VictoryChart,VictoryAxis,VictoryTheme,VictoryStack, VictoryLabel,VictoryContainer} from 'victory';
const barWidth = 26;
const labels = [{name:'Positive',color:'#38B954'},{name:'Neutral',color:'#e8e288'},{name:'Negative',color:'#f55'},{name:'Other',color:'#ddd'}];

const StockRecommendationChart = (props) => {
  const {data} = props;
  const positives=[],negatives=[],neutrals=[],others=[];
  let maxCount = 0;
  const names = data.map(({name,positive,negative,neutral,other} ) => {
    const total = positive+negative+neutral+other;
    maxCount = total > maxCount ? total : maxCount;
    positives.push({company:name,count:positive,total});
    negatives.push({company:name,count:negative});
    neutrals.push({company:name,count:neutral});
    others.push({company:name,count:other});
    return name;
  });
  console.log(names,positives,negatives,neutrals,others);
  const screenWidth = window.innerWidth;
  const chartWidth = Math.min(data.length*60, Math.max(screenWidth,350));
  const adjustFont = screenWidth >= 500 ? {} : {tickLabels:{fontSize:10}};
  return (
    <div className='container-center' style={{flexDirection:'column'}}>
      <VictoryChart height={maxCount} width={chartWidth} containerComponent={<VictoryContainer responsive={false}/>} domainPadding={barWidth} theme={VictoryTheme.material}>
        <VictoryAxis tickValues={[...Array(data.length).keys()]} tickFormat={names} style={adjustFont} />
        <VictoryAxis dependentAxis tickFormat={x=>x} />
        <VictoryStack animate={{duration:600,onLoad:{duration:100}}}>
          <VictoryBar labels={({datum:{count,total}})=>`${(count*100/total).toFixed(1)}%`} barWidth={barWidth} style={{data:{fill:'#38B954'},labels:{fill:"white",fontSize:8}}} labelComponent={<VictoryLabel dy={10}/>} data={positives} x="company" y="count" />
          <VictoryBar barWidth={barWidth} style={{data:{fill:'#e8e288'}}} data={neutrals} x="company" y="count" />
          <VictoryBar barWidth={barWidth} style={{data:{fill:'#f55'}}} data={negatives} x="company" y="count" />
          <VictoryBar barWidth={barWidth} style={{data:{fill:'#ddd'}}} data={others} x="company" y="count" />
        </VictoryStack>
      </VictoryChart>
      <div className={'container-center'}>
        {labels.map(item=><ChartLabel data={item} />)}
      </div>
    </div>
  )
}

const ChartLabel = ({data:{name,color}}) => {
  return <div className={'container-center m1'}>
    <div style={{backgroundColor:color,width:'0.6rem',height:'0.6rem',borderRadius:'50%',marginRight:'0.2rem'}} />
    <p>{name}</p>
  </div>
}

export default StockRecommendationChart
