import React, { useEffect, useState } from 'react';
import skillSet from '../data/mySkills';

const Header1 = ({section}) => {
  const {sectionName,description,icon,tint:{r,g,b}} = section;
  const iconTint = `rgba(${r},${g},${b},1)`;
  return (
    <div>
      <div className='container-center'>
        <i style={{fontSize:'1.6rem',marginRight:'0.2rem',color:iconTint}} className={`iconfont ${icon}`}></i>
        <h3>{sectionName}</h3>
      </div>
      <p className='txt1'>{description}</p>
    </div>
  )
}

const Item = ({item}) => {
  const {name,score} = item;
  return <div style={{display:'flex',margin:'1rem 0',borderRadius:'0.6rem',backgroundColor:'#fffce8'}}>
    <div style={{display:'flex',padding:'0.4rem',borderRadius:'0.6rem',backgroundColor:'#83a85f',flex:score,justifyContent:'space-between'}}>
      <span style={{color:'#fffce8'}}>{name}</span>
      <span style={{color:'#fffce8'}}>{score.toFixed(1)}</span>
    </div>
    <div style={{flex: (10-score)}} />
  </div>
}

const Me = () => {
  const [screenWidth,setScreenWidth] = useState(window.innerWidth);
  useEffect(()=>{
    const handleResizing = ({target:{innerWidth}}) => setScreenWidth(innerWidth);
    window.addEventListener('resize', handleResizing);
    return () => window.removeEventListener('resize', handleResizing);
  },[]);
  return (
    <div style={{flex:1,maxWidth:'850px',width:'100vw',margin:'0 auto'}}>
      <div id='me-container'>
        <div style={{display:'flex',flexDirection:'column',alignItems:'center',padding:'1rem'}}>
          <img style={{width:'150px',objectFit:'contain',borderRadius:'50%'}} src={require('../assets/me.png')} alt='chang'/>
          <p className='txt1' style={{marginTop:'1rem'}}>This is Chang Liu, a developer.</p>
          <p className='txt1' style={{marginTop:'1rem'}}>Take a look at my skill sets here {screenWidth>501?'ðŸ‘‰':'ðŸ‘‡'}</p>
        </div>
        <div style={{padding:'1rem'}}>
          {skillSet.map(item => {
            return (<div key={item.sectionName}>
              <Header1 section={item} />
              {item.data.map(skill=><Item key={skill.name} item={skill} />)}
            </div>)
          })}
        </div>
      </div>
    </div>
  )
}

export default Me
