import React, { useState, useEffect } from 'react';
import {getStockRecommendations} from '../data/remote';
import StockRecommendationChart from '../widgets/StockRecommendationChart'
import Spinner from '../widgets/Spinner';

const Home = () => {
  const [data, setData] = useState([]);
  const [err1, setError1] = useState('');

  useEffect(()=>{
    getStockRecommendations(d=>{setData(d);setError1('')},setError1);
  },[]);
  
  return (
    <div className='mx1' style={{flex:1,overflow:'hidden'}}>
      {err1!==''&&<div className='error-1'>Error:&nbsp;{err1}</div>}
      {data.length === 0 && err1==='' ? <Spinner /> : <StockRecommendationChart data={data} />}
    </div>
  )
}

export default Home
