import React, { useState, useEffect } from 'react';
// import {getAnalysis} from '../data/remote';

const Home = () => {
  const [data, setData] = useState([]);
  const [err1, setError1] = useState('');

  function x1() {
    let positive = 0, negative = 0, neutral = 0, other = 0;
    data.forEach((item,index)=>{
      const v = String(item.toGrade).toLowerCase();
      if (v.includes('hold') || v.includes('neutral') || v.includes('equal') || v === 'market perform' || v === 'sector weight' || v === 'sector perform' || v === 'perform') {
        neutral++;
      } else if(v.includes('positive') || v.includes('buy') || v.includes('out') || v.includes('over') || v.includes('add') || v.includes('accumulate')) {
        positive++;
      } else if (v.includes('negative') || v.includes('sell') || v.includes('under') || v.includes('weak') || v.includes('reduce')) {
        negative++;
      } else {
        other++;
        console.log('Not captured result: ', index, v);
      }
    });
    return {positive,negative,neutral,other}
  }

  // const stockNames = ['BABA','AAPL','MSFT','LK','VKTX','CPRX','FB','SRNE'];
  useEffect(()=>{
    //getAnalysis(stockNames[3],setData,setError1);
  },[]);
  const {positive,negative,neutral,other} = x1();

  return (
    <div className='container-center' style={{flex:1,flexDirection:'column'}}>
      <div className='error-1'>Error1:&nbsp;{err1}</div>
      <p>Total: {data.length}</p>
      <p>Positive: {positive}</p>
      <p>Negative: {negative}</p>
      <p>Neutral: {neutral}</p>
      <p>Other: {other}</p>
    </div>
  )
}

export default Home
